<template>
    <div id="listing">
    <div class="col-md-12 p-0">
        <form id="frmReport" method="post" target="iReport" asp-controller="ReportCommon" asp-action="ReportMain">
            <input type="hidden" id="reportName" name="reportName" value="CRMDetailReport" />
            <input type="hidden" id="hdnmodulename" name="moduleName" value="CRM" />
            <input type="hidden" name="crmType" id="hdncrmtype" value="Crmtype" />
            <div class="row">
                <div class="col-lg-4">
                    <div class="form-group">
                        <label>{{$t('Status')}}:</label>
                     <Multiselect v-model="StatusId" mode="tags" placeholder="Select" :close-on-select="true" label="status"
                                :filter-results="false" :min-chars="1" :resolve-on-load="false" :delay="0" :searchable="true" 
                                :options="statusList" track-by="value"  />   
                        <input type="hidden" id="StatusIds" name="statusIds" value="" :object="false" />
                    </div>
                </div>
                <div class="col-lg-4">
                    <div class="form-group">
                        <label>{{$t('ClientName')}}:</label>
                       
                        <Multiselect v-model="ClientId" mode="tags" placeholder="Select" :close-on-select="true" label="text"
                                :filter-results="false" :min-chars="1" :resolve-on-load="false" :delay="0" :searchable="true" 
                                :options="companyClientsByCompanyId" track-by="value"  />   
                              
                        <input type="hidden" id="ClientIds" name="ClientIds" value="" />
                    </div>
                </div>
                <div class="col-lg-4">
                    <div class="form-group">
                        <label>{{$t('MoneyValue')}}:</label>
                        <div class="row">
                            <div class="col-lg-6">
                                <div class="input-group">
                                    <input id="leadValueFrom" name="leadValueFrom" v-model="leadValueFrom" :placeholder="$t('From')" type="text" onkeydown="return AllowNumbersWithDot(event, this);" class="form-control" />
                                    <input type="hidden" id="amountFrom" name="amountFrom"   />
                                </div>
                            </div>
                            <div class="col-lg-6">
                                <div class="input-group">
                                    <input id="leadValueTo" name="leadValueTo" v-model="leadValueTo" :placeholder="$t('To')" type="text" onkeydown="return AllowNumbersWithDot(event, this);" class="form-control" />
                                    <input type="hidden" id="amountTo" name="amountTo"   />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-lg-6">
                    <div class="form-group">
                        <label>{{$t('DateRange')}}:</label>
                        <div class="row">
                            <div class="col-lg-6">
                                <div class="input-group">
                                    <v-date-picker  mode="date" :minute-increment="5" class="input-group"  v-model="dtFrom"  @dayclick="DependentDateObject()">
                                                            <template v-slot="{ inputValue, inputEvents,togglePopover }">
                                                                <input class="form-control"  :value="inputValue" v-on="inputEvents" :placeholder="$t('M/D/YYYY')" />
                                                                <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em class="fa fa-calendar"></em></span></div>
                                                            </template>
                                                        </v-date-picker>  
                                   
                                </div>
                            </div>
                            <div class="col-lg-6">
                                <div class="input-group">
                                    <v-date-picker  mode="date" :minute-increment="5" class="input-group" v-model="dtTo" :min-date="mindate">
                                                            <template v-slot="{ inputValue, inputEvents,togglePopover }">
                                                                <input class="form-control"  :value="inputValue" v-on="inputEvents" :placeholder="$t('M/D/YYYY')" />
                                                                <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em class="fa fa-calendar"></em></span></div>
                                                            </template>
                                                        </v-date-picker>  
                                  
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-lg-4">
                    <div class="form-group">
                        <label>
                        
                            <label>{{$t('LeadOwner')}}:</label>
                       
                        </label>
                      
                            <Multiselect v-if="Crmtype=='1'" v-model="OwnerIds" mode="tags" placeholder="Select" :close-on-select="true" label="name"
                                :filter-results="false" :min-chars="1" :resolve-on-load="false" :delay="0" :searchable="true" 
                                :options="companyleaderOwner" track-by="value"  />  

                    
                    
                   
                        <input type="hidden" id="OwnerIds" name="ownerIds" :value="$route.params.leadOwnerIds" />
                    </div>
                </div>
            </div>
            <div class="row flex-row-reverse mt-2">
                <div class="col-lg-6 text-right">
                    <a href="javascript:;" id="aSearch" :title="$t('Search')" name="aSearch" class="btn btn-success formbtn text-white mr-2" @click="onSearch">
                        <i class="fa fa-search pr-2"></i>{{$t('Search')}}</a>
                    <a href="javascript:;" :title="$t('Clear')" id="aClear" class="btn btn-danger formbtn widthhalf btnCancel" @click="onClear">
                        <i class="fa fa-close pr-2"></i>{{$t('Clear')}}</a>
                </div>
            </div>
        </form>
    </div>
    <div class="row placeholder">
        <div class="col-md-12 p-0">
           
            <ReportViewer  v-if="parameter.length>0" :parameter="parameter" :reportPath="reportPath"></ReportViewer>
        </div>
    </div>
</div>
</template>
<script>
import DataService from '../../../services/DataService';
import ReportViewer from './ReportViewer.vue';
export default {
    components: { ReportViewer },
    data() {
        return {
            statusList:[],
            companyleaderOwner:[],
            companyClientsByCompanyId:[],
            Crmtype:1,
            dtFrom: null,
            dtTo: null,
            reportPath: null,
            parameter: [],
            StatusId:null,
            ClientId:null,
            mindate:null,
            OwnerIds:null,
            leadValueTo:null,
            leadValueFrom:null,
          
           


        }
    },

    created:function()
    {
        var vm=this;
        vm.GetCompanyLeadOwners();
        vm.GetStatusList();
        vm.GetCompanyClientsByCompanyId();

    },
    methods: {

   GetCompanyLeadOwners:function(){
            var vm = this;
          var url = `leadId=0&type=P&moduleName=CRM`
            DataService.GetCompanyLeadOwners(url).then(response => {
               
               vm.companyleaderOwner= response.data;
           
            });
        },
        GetStatusList:function()
        {
            var vm = this;
          var url = `langCode=&statusType=CRM&hasglobal=false`
            DataService.GetStatusList(url).then(response => {
             vm.statusList= response.data;
     });


        },

        GetCompanyClientsByCompanyId:function(){
            var vm = this;
             DataService.GetCompanyClientsByCompanyId().then(response => {
            vm.companyClientsByCompanyId= response.data;
              
     });


        },
        onSearch: function () {
            var vm = this;
            var reportModel = {
                reportName: "crmdetailreport",
                moduleName: "crm",
                dateFrom: vm.dtFrom,
                dateTo: vm.dtTo,
                StatusIds:vm.StatusId.toString(),
                ClientIds:vm.ClientId.toString(),
                ownerIds:vm.OwnerIds.toString(),
                crmType:1,
                leadValueFrom:vm.leadValueFrom.toString(),
                leadValueTo:vm.leadValueTo.toString(),
              


            };
            var objectPostString = JSON.stringify(reportModel);
            DataService.ReportMain(objectPostString).then(response => {
                vm.reportPath = response.data.reportpath;
                vm.parameter = response.data.parameters;
            });
        },
        onClear: function () {
            this.dtFrom=null;
            this.dtTo=null;
            this.parameter=[];
            this.reportPath=null;
            this.StatusId=null;
            this.OwnerIds=null;
            this.ClientId=null;
            this.leadValueTo=null;
            this.leadValueFrom=null;
            
        },
        DependentDateObject: function () {
            this.dtTo = this.dtFrom;
            this.mindate=this.dtTo;
        }


    }
}
</script>