<template>
    <div id="listing">
    <div class="col-md-12 p-0">
        <form id="frmReport" method="post" target="iReport" asp-controller="ReportCommon" asp-action="ReportMain">
            <input type="hidden" id="reportName" name="reportName" value="RevenueReportByCRMDealStages" />
            <input type="hidden" id="hdnmodulename" name="moduleName" value="CRM" />
            <div class="row">
                <div class="col-lg-4">
                    <div class="form-group">
                        <label>{{$t('DealOwner')}}:</label>
                     
                        <Multiselect v-model="DSOwnerId" mode="tags" placeholder="Select" :close-on-select="true" label="name"
                                :filter-results="false" :min-chars="1" :resolve-on-load="false" :delay="0" :searchable="true" 
                                :options="CompanyDealOwners" track-by="value"  /> 
                        <input type="hidden" id="ownersIds" name="ownersIds" value="" />
                    </div>
                </div>
                <div class="col-lg-6">
                    <div class="form-group">
                        <label>{{$t('DateRange')}}:</label>
                        <div class="row">
                            <div class="col-lg-6">
                                <div class="input-group">
                                    <v-date-picker  :minute-increment="5"  class="input-group"
                                     v-model="dtFrom" @dayclick="DependentDateObject()">
                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                <input v-bind:class="{'form-control' : true}" :value="inputValue" v-on="inputEvents"  placeholder="M/D/YYYY"
                                                   />
                                                <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em 
                                                            class="fa fa-calendar"></em></span></div>
                                            </template>
                                        </v-date-picker>      
                               
                                </div>
                            </div>
                            <div class="col-lg-6">
                                <div class="input-group">
                                    <v-date-picker  :minute-increment="5"  class="input-group"
                                    v-model="dtTo" :min-date="mindate">
                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                <input v-bind:class="{'form-control' : true}" :value="inputValue" v-on="inputEvents"  placeholder="M/D/YYYY"
                                                   />
                                                <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em 
                                                            class="fa fa-calendar"></em></span></div>
                                            </template>
                                        </v-date-picker>   
                                   
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="row flex-row-reverse mt-2">
                <div class="col-lg-6 text-right">
                    <a href="javascript:;" id="aSearch" :title="$t('Search')" name="aSearch" class="btn btn-success formbtn text-white mr-2" @click="onSearch">
                        <i class="fa fa-search pr-2"></i>{{$t('Search')}}</a>
                    <a href="javascript:;" :title="$t('Clear')" id="aClear" class="btn btn-danger formbtn widthhalf btnCancel" @click="onClear">
                        <i class="fa fa-close pr-2"></i>{{$t('Clear')}}</a>
                </div>
            </div>
        </form>
    </div>
    <div class="row placeholder">
        <div class="col-md-12 p-0">
            <div class="col-md-12 p-0">
         <ReportViewer  v-if="parameter.length>0" :parameter="parameter" :reportPath="reportPath"></ReportViewer>
        </div>
        </div>
    </div>
</div>
</template>
<script>
import DataService from '../../../services/DataService';
import ReportViewer from './ReportViewer.vue';

export default {
    components: { ReportViewer },
     data() {
         return {
            CompanyDealOwners: [],
            dtFrom: null,
            dtTo: null,
            reportPath: null,
            parameter: [],
            DSOwnerId:null,
            mindate:null,
         
 
         }
     },
     created: function () {
    this.GetCompanyDealOwners();
     },
     methods: {
        GetCompanyDealOwners:function(){
            var vm = this;
            var url = `leadId=0&type=0&moduleName=CRM`
            DataService.GetCompanyDealOwners(url).then(response => {
             vm.CompanyDealOwners= response.data;
           
            });
        },
        onSearch: function () {
            var vm = this;
            var reportModel = {
                reportName: "revenuereportbycrmdealstages",
                moduleName: "crm",
                dateFrom: vm.dtFrom,
                dateTo: vm.dtTo,
                ownerIds:vm.DSOwnerId.toString(),
            };
            var objectPostString = JSON.stringify(reportModel);
            DataService.ReportMain(objectPostString).then(response => {
                vm.reportPath = response.data.reportpath;
                vm.parameter = response.data.parameters;
            });
        },
        onClear: function () {
            this.dtFrom=null;
            this.dtTo=null;
            this.parameter=[];
            this.reportPath=null;
            this.DSOwnerId=null;
        },
        DependentDateObject: function () {
            this.dtTo = this.dtFrom;
            this.mindate=this.dtTo;
        }
        
     }
 }
 
</script>