import QuotationReport from "./QuotationReport.vue";
import RevenueReportByChannelSource from "./RevenueReportByChannelSource.vue";
import RevenueReportByDeal from "./RevenueReportByDeal.vue";
import RevenueReportByStages from "./RevenueReportByStages.vue";
import $ from "jquery";
export default {
    components: { DealOwner,DealStage,CRMDetail,QuotationReport ,RevenueReportByChannelSource,RevenueReportByDeal,RevenueReportByStages},
    data() {
        return {
            UserLicenseModules: null,
            ConfgurationValueByKey: null,
            activeKey: 1,
            isLoading:false,
        };
    },
    created: function () {
        this.GetUserLicenseModulesReport();
    },
    mounted:function ()
    {           
        $(".searchcustomfilter").keyup(function (event) {
        event.stopImmediatePropagation();
        var val = $(this).val();
        $("ul#upper").find("li").each(function () {
            val = String(val).toLowerCase();
            var text = String($(this).find("a").text()).toLowerCase();
            if (text.includes(val)) {
                $(this).closest("li").show();
            }
            else {
                $(this).closest("li").hide();
            }
        });
    });
        $("#reportstabsdata").on("click", "a.nav-link", function (e) {
            e.stopImmediatePropagation();
            $("#spnreporttext").html($(this).text());
        //  vm.loadCRMDetailReport(actionname, nameprivilege, $(this));
        });
    },
    methods: {
        GetPrivilege: function (param) {
           (async()=>{
          await  DataService.CheckPrivilege(param)
                .then(function (response) {
            });
        })
        },
        GetUserLicenseModulesReport: function () {
            var vm = this;
            vm.isLoading=true;
            DataService.GetUserLicenseModulesReport().then(response => {
                vm.isLoading=false;
                vm.UserLicenseModules = response.data.list;
            });
        },
        GetConfgurationValueByKey: function () {
            var vm = this;
            DataService.GetConfgurationValueByKey().then(response => {
                vm.ConfgurationValueByKey = response.data.list;
            });
        },
        loadCRMDetailReport: function (actionname, nameprivilege, ctrl) {
            showLoader();
            $($(this).attr('href')).hide();
            var crmType = "";
            if (actionname == "CRMConsolidateReport") {
                this.oadCRMDetail(crmType, actionname);
            }
            else {
                if (this.GetPrivilege('controller=CRMReport&action='+actionname)) {
                    if (actionname == "LeadReport") {
                        actionname = "CRMDetail";
                        crmType = "l";
                    }
                    this.loadCRMDetail(crmType, actionname);
                }
                else {
                    $("#DisplayReport").html('<h2>Sorry, You are not authorized to access this section</h2>');
                }
            }
        
        },
      

    }    
}

</script>