<template>
<div id="listing">
    <div class="col-md-12 p-0">
        <form id="frmReport" method="post" target="iReport" asp-controller="ReportCommon" asp-action="ReportMain">
            <input type="hidden" id="reportName" name="reportName" value="RevenueReportByCRMDeal" />
            <input type="hidden" id="hdnmodulename" name="moduleName" value="CRM" />
            <input type="hidden" id="hdnownerid" name="ownerid" value="CRM" />
            <div class="row">
                <div class="col-lg-4">
                    <div class="form-group">
                        <label>{{$t('LeadOwners')}}:</label>
                        
                            <Multiselect v-if="usertype=='CA'" v-model="RRBDOwnerId" mode="tags" placeholder="Select" :close-on-select="true" label="name"
                                :filter-results="false" :min-chars="1" :resolve-on-load="false" :delay="0" :searchable="true" 
                                :options="companyleaderOwner" track-by="value"  />   
                            <!-- <select  id = "RRBDOwnerId", multiple = "multiple", class = "form-control multiselectdd select" asp-items="@(new SelectList(objRepo.GetCompanyLeadOwners(loggedUserInfo.CompanyId, 0, '0', "CRM"), "Value", "Name"))"></select> -->
                  
                            <Multiselect v-else  v-model="LoggedOwnerId" mode="tags" placeholder="Select" :close-on-select="true" label="name"
                                :filter-results="false" :min-chars="1" :resolve-on-load="false" :delay="0" :searchable="true" 
                                :options="companyleaderOwner" track-by="value" disabled = "disabled"  /> 
                            <!-- <select  disabled = "disabled", id = "LoggedOwnerId", multiple = "multiple", class = "form-control multiselectdd select" asp-items="@(new SelectList(objRepo.GetCompanyLeadOwners(loggedUserInfo.CompanyId, 0, '0', "CRM"), "Value", "Name"))"></select> -->
                
                        <input type="hidden" id="ownersIds" name="ownersIds" value="" />
                    </div>
                </div>
                <div class="col-lg-4">
                    <div class="form-group">
                        <label>{{$t('Channels')}}:</label>
                        <Multiselect v-model="Channelid" mode="tags" placeholder="Select" :close-on-select="true" label="name"
                                :filter-results="false" :min-chars="1" :resolve-on-load="false" :delay="0" :searchable="true" 
                                :options="CompanyReportDDLTypes" track-by="value"  />  
                        <!-- <select  id = "Channelid", multiple = "multiple", class = "form-control multiselectdd select" asp-items="@(new SelectList(objRepo.GetCompanyReportDDLTypes(loggedUserInfo.CompanyId, "CRM_CHANNELS", "CRM"), "Value", "Name"))"></select> -->
                        <input type="hidden" id="channelids" name="channelids" value="" />
                    </div>
                </div>
                <div class="col-lg-4">
                    <div class="form-group">
                        <label>{{$t('Stages')}}:</label>
                        <Multiselect v-model="Stageid" mode="tags" placeholder="Select" :close-on-select="true" label="name"
                                :filter-results="false" :min-chars="1" :resolve-on-load="false" :delay="0" :searchable="true" 
                                :options="ddlstage" track-by="value"  /> 
                        <!-- <select  id = "Stageid", multiple = "multiple", class = "form-control multiselectdd select" asp-items="@(new SelectList(objRepo.GetDDLStages(loggedUserInfo.CompanyId), "Value", "Name"))"></select> -->
                        <input type="hidden" id="stageids" name="stageids" value="" />
                    </div>
                </div>
                <div class="col-lg-6">
                    <div class="form-group">
                        <label>{{$t('DateRange')}}:</label>
                        <div class="row">
                            <div class="col-lg-6">
                                <div class="input-group">
                                    <v-date-picker  :minute-increment="5"  class="input-group"
                                    v-model="dtFrom" @dayclick="DependentDateObject()">
                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                <input v-bind:class="{'form-control' : true}" :value="inputValue" v-on="inputEvents"  placeholder="M/D/YYYY"
                                                   />
                                                <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em 
                                                            class="fa fa-calendar"></em></span></div>
                                            </template>
                                        </v-date-picker>   
                                
                                </div>
                            </div>
                            <div class="col-lg-6">
                                <div class="input-group">
                         
                                    <v-date-picker :minute-increment="5"  class="input-group"
                                    v-model="dtTo" :min-date="mindate">
                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                <input v-bind:class="{'form-control' : true}" :value="inputValue" v-on="inputEvents"  placeholder="M/D/YYYY"
                                                   />
                                                <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em 
                                                            class="fa fa-calendar"></em></span></div>
                                            </template>
                                        </v-date-picker>  
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="row flex-row-reverse mt-2">
                <div class="col-lg-6 text-right">
                    <a href="javascript:;" id="aSearch" :title="$t('Search')" name="aSearch" class="btn btn-success formbtn text-white mr-2" @click="onSearch">
                        <i class="fa fa-search pr-2"></i>{{$t('Search')}}</a>
                    <a href="javascript:;" :title="$t('Clear')" id="aClear" class="btn btn-danger formbtn widthhalf btnCancel" @click="onClear">
                        <i class="fa fa-close pr-2"></i>{{$t('Clear')}}</a>
                </div>
            </div>
        </form>
    </div>
    <div class="row placeholder">
        <div class="col-md-12 p-0">
            <ReportViewer  v-if="parameter.length>0" :parameter="parameter" :reportPath="reportPath"></ReportViewer>
        </div>
    </div>
</div>

</template>
<script>
import DataService from '../../../services/DataService';
import ReportViewer from './ReportViewer.vue';
export default {
    components: { ReportViewer },
    data() {
        return {
            usertype: null,
            companyleaderOwner:[],
            CompanyReportDDLTypes:[],
            ddlstage:[],
            dtFrom: null,
            dtTo: null,
            reportPath: null,
            parameter: [],
            RRBDOwnerId:null,
            LoggedOwnerId:null,
            mindate:null,
            Channelid:null,
            Stageid:null,
           
        }
    },
    created: function () {
  
        this.usertype = this.GetUserInfo.UserType,
        this.GetCompanyLeadOwners();
        this.GetCompanyReportDDLTypes();
        this.GetDDLStages();
    },
    methods: {
        GetCompanyLeadOwners:function(){
            var vm = this;
          var url = `leadId=0&type=0&moduleName=CRM`
            DataService.GetCompanyLeadOwners(url).then(response => {
               
               vm.companyleaderOwner= response.data;
           
            });
        },
        GetCompanyReportDDLTypes:function(){
            var vm = this;
         var url = `type=CRM_CHANNELS&moduleName=CRM`
            DataService.GetCompanyReportDDLTypes(url).then(response => {
              vm.CompanyReportDDLTypes= response.data;
           
            });
        },
        GetDDLStages:function(){
            var vm = this;
      DataService.GetDDLStages().then(response => {
         vm.ddlstage= response.data;
           
            });
        },
        onSearch: function () {
            var vm = this;
            var reportModel = {
                reportName: "revenuereportbycrmdeal",
                moduleName: "crm",
                dateFrom: vm.dtFrom,
                dateTo: vm.dtTo,
                ownerIds:vm.usertype=='CA'? vm.RRBDOwnerId.toString(): vm.LoggedOwnerId.toString(),
                channelids:vm.Channelid.toString(),
                stageIds:vm.Stageid.toString()
                
            };
            var objectPostString = JSON.stringify(reportModel);
            DataService.ReportMain(objectPostString).then(response => {
                vm.reportPath = response.data.reportpath;
                vm.parameter = response.data.parameters;
            });
        },
        onClear: function () {
            this.dtFrom=null;
            this.dtTo=null;
            this.parameter=[];
            this.reportPath=null;
            this.RRBDOwnerId=null;
            this.channelids=null;
            this.LoggedOwnerId=null;
            this.Stageid=null;
        },
        DependentDateObject: function () {
            this.dtTo = this.dtFrom;
            this.mindate=this.dtTo;
        }
     
       
    }
}

</script>